.user__link {
  display: flex;
  align-items: center;
  padding: 10px 0px;
  gap: 5px;
}

.promo {
  min-height: 628px;
  background-color: #0d113a;
  color: #ffffff;
  position: relative;
}
.promo::before {
  content: "";
  background: url(/img/bg-lines.svg);
  position: absolute;
  top: 170px;
  left: 0px;
  bottom: 0px;
  right: 0px;
}
.promo__wrapper {
  display: flex;
  flex-direction: column;
  max-width: 807px;
  padding-top: 75px;
}
.promo__numbers {
  font-weight: 400;
  font-size: 36px;
  line-height: 53px;
  color: #6dbedf;
}
.promo__date {
  font-weight: 400;
  font-size: 20px;
  line-height: 29px;
  margin-left: 6px;
  color: #6dbedf;
}
.promo h1 {
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 53px;
  color: #ffffff;
  margin-top: 24px;
  margin-bottom: 36px;
}

.info {
  position: relative;
  top: -100px;
  z-index: 1;
  color: #0d113a;
}

.info__top--item {
  display: flex;
  flex-direction: column;
  gap: 16px;
  background-color: #6dbedf;
  padding: 25px;
  height: 100%;
}
.info__top--item h3 {
  font-weight: 700;
  font-size: 22px;
  line-height: 32px;
  color: #0d113a;
}
.info__top--item p {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #0d113a;
}

.info__main--item {
  display: flex;
  flex-direction: column;
  height: 100%;
  gap: 32px;
  background-color: #ffffff;
  padding: 37px 50px;
}
.info__main--item h2 {
  font-weight: 800;
  font-size: 32px;
  line-height: 47px;
  text-transform: uppercase;
  color: #0d113a;
}
.info__main--item p {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}
.info__main--item ol {
  font-weight: 400;
  font-size: 16px;
  line-height: 28px;
  list-style: auto;
  color: #0d113a;
}

.organizers {
  background-color: #272B6A;
  padding: 74px 0px;
}
.organizers h2 {
  margin-bottom: 33px;
  font-weight: 800;
  font-size: 32px;
  line-height: 47px;
  text-transform: uppercase;
  color: #ffffff;
}
.organizers__item {
  display: flex;
  align-items: center;
  gap: 6px;
  background-color: #ffffff;
  color: #0d113a;
  padding: 32px 56px;
}
.organizers__item:hover {
  box-shadow: 0px 10px 20px rgba(34, 34, 34, 0.05);
}

.events {
  padding: 56px 0px 96px;
}
.events h2 {
  font-weight: 800;
  font-size: 32px;
  line-height: 47px;
  text-transform: uppercase;
  color: #0d113a;
}

.btn-events-wrapper {
  display: flex;
  gap: 23px;
  margin: 40px 0px;
}

.btn-events {
  padding: 13px 23px;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  color: #00a1d5;
  border: 1px solid #00a1d5;
}
.btn-events--active {
  background: #00a1d5;
  color: #ffffff;
}

.events__item {
  display: flex;
  flex-direction: column;
  gap: 26px;
  height: 100%;
  padding: 30px 20px;
  background-color: #ffffff;
  cursor: pointer;
}
.events__item p {
  font-weight: 400;
  font-size: 22px;
  line-height: 32px;
  color: #000000;
}
.events__item:hover {
  box-shadow: 0px 10px 20px rgba(34, 34, 34, 0.05);
}

.events__time {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #6a7277;
}

.events__place {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  margin-left: 13px;
  color: #00a1d5;
}

.speakers {
  padding: 64px 0px 100px;
  background-color: #272B6A;
  color: #ffffff;
  position: relative;
}
.speakers::before {
  content: "";
  background: url(/img/bg-lines.svg);
  position: absolute;
  top: 350px;
  left: 0px;
  bottom: 0px;
  right: 0px;
}
.speakers h2 {
  font-weight: 800;
  font-size: 32px;
  line-height: 47px;
  text-transform: uppercase;
}

.speakers__item {
  display: flex;
  flex-direction: column;
  gap: 32px;
  padding: 0 15px;
}
.speakers__item p {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.speakers__name {
  font-weight: 800;
  font-size: 22px;
  line-height: 41px;
}

.moderators {
  padding: 64px 0px 120px;
  background-color: #0d113a;
  color: #ffffff;
  position: relative;
}
.moderators::before {
  content: "";
  background: url(/img/bg-lines.svg);
  position: absolute;
  top: 350px;
  left: 0px;
  bottom: 0px;
  right: 0px;
}
.moderators h2 {
  font-weight: 800;
  font-size: 32px;
  line-height: 47px;
  text-transform: uppercase;
  margin-bottom: 54px;
}

.moderators__item {
  display: flex;
  flex-direction: column;
  gap: 32px;
  padding: 0 15px;
}
.moderators__item p {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.moderators__name {
  font-weight: 800;
  font-size: 22px;
  line-height: 41px;
}

.news {
  padding: 60px 0px 84px;
}
.news h2 {
  font-weight: 800;
  font-size: 32px;
  line-height: 47px;
  text-transform: uppercase;
  margin-bottom: 41px;
  color: #0d113a;
}
.news__item {
  display: flex;
  flex-direction: column;
  gap: 26px;
  max-width: 460px;
  margin: 0 auto;
  background-color: #ffffff;
}
.news__item span {
  font-weight: 400;
  font-size: 22px;
  line-height: 32px;
  margin-left: 20px;
  color: #6a7277;
}
.news__item p {
  font-weight: 400;
  font-size: 20px;
  line-height: 29px;
  margin-left: 20px;
  margin-bottom: 15px;
  color: #0d113a;
}

.history {
  padding: 54px 0px;
  background-color: #ffffff;
}
.history h2 {
  font-weight: 800;
  font-size: 32px;
  line-height: 47px;
  text-transform: uppercase;
  margin-bottom: 32px;
  color: #0d113a;
}

.map {
  position: relative;
  color: #0d113a;
  overflow: hidden;
}
.map__info {
  position: absolute;
  top: 55px;
  left: 5%;
  background-color: #ffffff;
  padding: 40px;
}
.map__info h2 {
  font-weight: 800;
  font-size: 28px;
  line-height: 41px;
  text-transform: uppercase;
  margin-bottom: 20px;
}
.map__info p {
  font-weight: 400;
  font-size: 20px;
  line-height: 29px;
  margin-bottom: 18px;
}
.map__info a {
  color: #00a1d5;
  padding-left: 4px;
}

.map__place {
  font-style: normal;
  font-weight: 700;
  font-size: 22px;
  line-height: 32px;
  margin-bottom: 12px;
}

.map__adress {
  font-weight: 400;
  font-size: 20px;
  line-height: 29px;
  margin-bottom: 52px;
}

.question-wrapper {
  display: flex;
  justify-content: space-between;
}

.question-main {
  display: flex;
  flex-direction: column;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  background-color: #ffffff;
  color: #0d113a;
  padding: 50px 11px 410px;
}
.question-main h2 {
  font-size: 32px;
  line-height: 47px;
  text-transform: uppercase;
  margin-bottom: 45px;
}
.question-main h3 {
  font-weight: 700;
  font-size: 22px;
  line-height: 32px;
  margin-bottom: 24px;
}
.question-main p {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  border-left: 1px solid #00a1d5;
  padding-left: 34px;
  padding-bottom: 62px;
  margin-left: 40px;
}

footer {
  background-color: #0d113a;
  padding-top: 50px;
  color: #ffffff;
}
footer h2 {
  font-weight: 400;
  font-size: 22px;
  line-height: 21px;
  padding-bottom: 18px;
  margin-bottom: 32px;
  border-bottom: 1px solid #323852;
  color: #323852;
}
footer li a {
  font-weight: 400;
  font-size: 16px;
  line-height: 32px;
  color: #ffffff;
}

.for-user-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.contacts-item {
  display: flex;
  grid-gap: 14px;
  margin-bottom: 26px;
}

.hidden {
  display: none;
}

.modals {
  position: fixed;
  top: -2000px;
  width: 100%;
  z-index: 9;
  background-color: rgba(255, 255, 255, 0.75);
  transition: all 0.5s ease;
}
.modals.active {
  top: 100px;
}

.modals-content {
  z-index: 9;
  width: 100%;
  background-color: #ffffff;
  display: flex;
  justify-content: space-between;
}

.modals-menu {
  background-color: #272B6A;
  padding-top: 50px;
  color: #ffffff;
  width: 100%;
}
.modals-menu h2 {
  font-weight: 400;
  font-size: 22px;
  line-height: 21px;
  padding-bottom: 18px;
  margin-bottom: 32px;
  border-bottom: 1px solid #55598f;
  color: #55598f;
}
.modals-menu li a {
  font-weight: 400;
  font-size: 16px;
  line-height: 32px;
  color: #ffffff;
}

.for-user-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.contacts-item {
  display: flex;
  grid-gap: 14px;
  margin-bottom: 26px;
}

.modal-auth {
  display: none;
  box-sizing: border-box;
  position: fixed;
  width: 490px;
  padding: 28px 40px;
  background-color: white;
  box-shadow: 0 20px 40px 0 rgba(41, 52, 73, 0.75);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
}

.modal-auth h2 {
  font-style: normal;
  font-weight: 500;
  font-size: 50px;
  line-height: 100%;
}

.inner-menu {
  background: #272b6a;
  padding-top: 23px;
}
.inner-menu a {
  display: block;
  font-weight: 400;
  font-size: 18px;
  line-height: 60px;
  padding: 0px 77px 0px 32px;
  color: #ffffff;
}
.inner-menu a:hover {
  font-weight: 400;
  font-size: 18px;
  line-height: 60px;
  padding: 0px 77px 0px 32px;
  background-color: #ffffff;
  color: #0d113a;
}